import React from "react";

const OqueFazemos = ({conteudoOqueFazemos}) => {

    return (
        <section className="section-oque-fazemos animation" id="oque-fazemos">
            <h1 className="circle-title-section">O que fazemos</h1>
            <div className="container-oque-fazemos">
                {
                    conteudoOqueFazemos.map(res => {
                        return (
                            <div key={res.id} className={res.class}>
                                <p>{res.title}</p>
                            </div>
                        );
                    })
                }   
            </div>
        </section>
    );
}

export default OqueFazemos;