import React, { useState } from "react";

const Servicos = ({ conteudoServicos }) => {

    const [alterarImg, setAlterarImg] = useState(1);

    return (
        <>
            <section className="section-servicos animation" id="servicos">
                <h1 className="circle-title-section">Servios realizados</h1>
                <div className="container-servicos">
                {
                    conteudoServicos.map(res => {
                        return (
                            <>
                                <h1 key={res.id} id={res.anchor}>
                                    {res.title}
                                </h1>
                                <div key={res.id} className="images animation">
                                    {
                                        res.img.map(resimg => {

                                            return (  
                                                    
                                                <div 
                                                    key={resimg[1]} 
                                                    style={{ backgroundImage: `url(${resimg[0]})` }}
                                                    onClick={() => setAlterarImg(!alterarImg)}
                                                ></div>        
                                                
                                            )
                                                
                                        })
                                    }
                                </div>
                            </>
                        )
                    })
                }
                </div>
            </section>
        </>
    );
}

export default Servicos;