import React from "react";
import Anchor from "antd/lib/anchor";

const Home = () => {
    const { Link } = Anchor;

    return (
        <>
            <section className="section-home" id="home">
                <div className="conteudo-container">
                    <div className="logo-text">
                        <h1>kairs</h1>
                        <h1>construo civil</h1>
                        <h2>construindo os seus sonhos</h2>
                    </div>
                    <Anchor targetOffset={100}>
                        <Link href="#oque-fazemos" className="saiba-mais" title="saiba mais" />
                    </Anchor>
                </div>
            </section>
        </>
    );
}

export default Home;