import React, { useEffect } from "react";

import Header from "./Navegacao";
import Home from "./SectionHome";
import QuemSomos from "./SectionQuemSomos";
import OqueFazemos from "./SectionOqueFazemos";
import Servicos from "./SectionServicos";
import Rodape from "./Rodape";

import imagesServicos from "./images-servicos/objectImages";

import './styles-components/styleSections.scss';
import './styles-components/styleSectionsMobile.scss';
import './styles-components/styleScrollAnimation.scss';

const AllSections = () => {

    useEffect(() => {
        const observer = new IntersectionObserver( entries => {
            Array.from(entries).forEach(entry => {
                if(entry.isIntersecting) {
                    entry.target.classList.add('on');
                }    
            })
        })
    
        
        Array.from(document.querySelectorAll('.animation')).forEach( e => {
            observer.observe(e);
        })
    })

    const conteudoOqueFazemos = [
        { id: 1, class: 'construcao', title: 'construes' },
        { id: 2, class: 'reforma', title: 'reformas' },
        { id: 3, class: 'acabamento', title: 'acabamentos' },
        { id: 4, class: 'pintura', title: 'pinturas' },
        { id: 5, class: 'hidraulica', title: 'hidraulicas' },
        { id: 6, class: 'eletrica', title: 'eletricas' }
    ];

    const servicos = [
        {   
            id: 1,
            title: 'Construes', 
            anchor: 'servicos-construcoes',
            img: [ 
            [imagesServicos.imagesConstrucao[0], '1'], 
            [imagesServicos.imagesConstrucao[1], '2'],  
            [imagesServicos.imagesConstrucao[2], '3'], 
            [imagesServicos.imagesConstrucao[3], '4'], 
            [imagesServicos.imagesConstrucao[4], '5'], 
            [imagesServicos.imagesConstrucao[5], '6'] 
            ]
        },

        { 
            id: 2,
            title: 'Reformas',
            anchor: 'servicos-reformas', 
            img: [
                [imagesServicos.imagesReforma[0], '7'], 
                [imagesServicos.imagesReforma[1], '8'], 
                [imagesServicos.imagesReforma[2], '9'], 
                [imagesServicos.imagesReforma[3], '10'], 
                [imagesServicos.imagesReforma[4], '11'], 
                [imagesServicos.imagesReforma[5], '12'] 
            ]
        },

        { 
            id: 3,
            title: 'Acabamentos',
            anchor: 'servicos-acabamentos',
            img: [
                [imagesServicos.imagesAcabamento[0], '13'],  
                [imagesServicos.imagesAcabamento[1], '14'],  
                [imagesServicos.imagesAcabamento[2], '15'],  
                [imagesServicos.imagesAcabamento[3], '16'],  
                [imagesServicos.imagesAcabamento[4], '17'],  
                [imagesServicos.imagesAcabamento[5], '18']
            ]
        },

        { 
            id: 4,
            title: 'Pintura',
            anchor: 'servicos-pinturas',
            img: [
                [imagesServicos.imagesPinturas[0], '19'],
                [imagesServicos.imagesPinturas[1], '20'],
                [imagesServicos.imagesPinturas[2], '21'],
                [imagesServicos.imagesPinturas[3], '22'],
                [imagesServicos.imagesPinturas[4], '23'],
                [imagesServicos.imagesPinturas[5], '24']
            ]
        },

        {
            id: 5,
            title: 'Hidrulica',
            anchor: 'servicos-hidrulicas',
            img: [
                [imagesServicos.imagesHidraulicas[0], '25'],
                [imagesServicos.imagesHidraulicas[1], '26'],
                [imagesServicos.imagesHidraulicas[2], '27'],
                [imagesServicos.imagesHidraulicas[3], '28'],
                [imagesServicos.imagesHidraulicas[4], '29'],
                [imagesServicos.imagesHidraulicas[5], '30']
            ]
        },

        {
            id: 6,
            title: 'Eltrica',
            anchor: 'servicos-eletricas',
            img: [
                [imagesServicos.imagesEletricas[0], '31'],
                [imagesServicos.imagesEletricas[1], '32'],
                [imagesServicos.imagesEletricas[2], '33'],
                [imagesServicos.imagesEletricas[3], '34'],
                [imagesServicos.imagesEletricas[4], '35'],
                [imagesServicos.imagesEletricas[5], '36'],
            ]
        }
    ];

    return (
        <>
        <Header />
        <div className="background-conteudo">
            <Home />
            <QuemSomos />
            <OqueFazemos 
                conteudoOqueFazemos={conteudoOqueFazemos}
            />
            <Servicos 
                conteudoServicos={servicos}
            />
            <Rodape />
        </div>
        </>
    );
}

export default AllSections;