import React, { useEffect, useState } from "react";
import Anchor from "antd/lib/anchor";

const Header = () => {
    
    const { Link } = Anchor;

    // Abrir menu hamburguer
    const [btnMenu, setBtnMenu] = useState(false);
    useEffect(() => {
        const menu = document.querySelector('.navegacao-mob').style;
        const containerMenu = document.querySelector('.container-menu').style;
        const btnMenuPosition = document.querySelector('.btn-menu').style;

        const btnMenuHamburguer= document.querySelector('.menuHamburguer').style;
        const btnFecharMenu = document.querySelector('.fecharMenu').style;

        if(btnMenu === false){
            btnMenuPosition.right = '40px';
            btnMenuPosition.top = '22px';
            btnMenuPosition.borderRadius = '5px'

            btnMenuHamburguer.display = 'block';
            btnFecharMenu.display = 'none';

            containerMenu.left = '100vw';
            menu.right = '-100vw';
        } else {
            btnMenuPosition.right = '25px';
            btnMenuPosition.top = '22px';

            btnMenuHamburguer.display = 'none';
            btnFecharMenu.display = 'block';

            containerMenu.left = '0';
            menu.right = '-5vw';
        }
    }, [btnMenu])


    // Abrir menu servicos
    const [btnServicos, setBtnServicos] = useState(false);
    useEffect(() => {
        const containerServicos = document.querySelector('.servicos-menu').style;

        if(btnServicos === false){
            containerServicos.display = 'none';
        } else {
            containerServicos.display = 'flex';
        }

    }, [btnServicos])

    return (
        <>
            <header className="header-desktop"> 
                <h1>kairs</h1>
                <nav  className="navegacao">
                    <Anchor targetOffset={100}>
                        <ul>
                            <li><Link href="#home" title="Home" /></li>
                            <li><Link href="#quem-somos" title="Quem somos" /></li>
                            <li><Link href="#oque-fazemos" title="O que fazemos" /></li>
                            <li className="servicos"><Link href="#servicos" title="Servios" />
                                <ul>
                                    <li><Link href="#servicos-construcoes" title="construes" /></li>
                                    <li><Link href="#servicos-reformas" title="reformas" /></li>
                                    <li><Link href="#servicos-acabamentos" title="acabamentos" /></li>
                                    <li><Link href="#servicos-pinturas" title="pinturas" /></li>
                                    <li><Link href="#servicos-hidrulicas" title="hidrulicas" /></li>
                                    <li><Link href="#servicos-eletricas" title="eltricas" /></li>
                                </ul>
                            </li>   
                            <li><Link href="#contato" title="Contato" /></li>
                        </ul>
                    </Anchor>
                </nav>
            </header>

            <header className="header-mobile">
                <h1>kairs</h1>
                <div className="btn-menu" onClick={() => setBtnMenu(!btnMenu)}>
                    <div className="menuHamburguer"></div>
                    <p className="fecharMenu">X</p>
                </div>
                <div className="container-menu" onClick={() => setBtnMenu(!btnMenu)}></div>
                <nav  className="navegacao-mob">
                    <Anchor targetOffset={100}>
                    <ul>
                        <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#home" title="Home" /></li>
                        <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#quem-somos" title="Quem somos" /></li>
                        <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#oque-fazemos" title="O que fazemos" /></li>
                        <li className="servicos" onClick={() => setBtnServicos(!btnServicos)}><Link href="#servicos" title="Servios" />
                            <ul className="servicos-menu">
                                <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#servicos-construcoes" title=" construes" /></li>
                                <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#servicos-reformas" title=" reformas" /></li>
                                <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#servicos-acabamentos" title=" acabamentos" /></li>
                                <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#servicos-pinturas" title=" pinturas" /></li>
                                <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#servicos-hidrulicas" title=" hidrulicas" /></li>
                                <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#servicos-eletricas" title=" eltricas" /></li>
                            </ul>
                        </li>   
                        <li onClick={() => setBtnMenu(!btnMenu)}><Link href="#contato" title="Contato" /></li>
                    </ul>
                    </Anchor>
                </nav>
            </header>
        </>
    );
}

export default Header;